.App {
  font-family: 'Arial', sans-serif;
  padding: 20px;
  background-color: #2E2A47;
  color: white;
  min-height: 100vh;
}

.header {
  text-align: center;
  background-color: #563d2b;
  padding: 20px;
  border-radius: 8px;
}

h1 {
  margin: 0;
  font-size: 2.5rem;
  color: white;
}

.main-content {
  margin-top: 20px;
}

.form-container {
  background-color: #3d2b1f;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 30px;
}

input {
  display: block;
  margin: 10px 0;
  padding: 10px;
  width: 100%;
  max-width: 350px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 1rem;
}

button {
  background-color: #795c39;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 1rem;
}

button:hover {
  background-color: #563d2b;
}

.funcionarios-list {
  background-color: #3d2b1f;
  padding: 20px;
  border-radius: 8px;
}

.funcionarios-list ul {
  list-style-type: none;
  padding: 0;
}

.funcionarios-list li {
  margin: 15px 0;
  background-color: #2d1b12;
  padding: 15px;
  border-radius: 8px;
}

.lgpd-section, .export-section {
  margin-top: 20px;
  text-align: center;
}

.lgpd-info {
  background-color: #563d2b;
  padding: 15px;
  border-radius: 8px;
  margin-top: 10px;
}

.footer {
  text-align: center;
  padding: 15px;
  margin-top: 40px;
  background-color: #563d2b;
  color: white;
}

export-section button {
  background-color: #008CBA;
  font-size: 1rem;
}

export-section button:hover {
  background-color: #006f8e;
}

